:root {
  --bg-color: #303030;
  --color-first: #E89E2E;
  --color-secund: #2EE854;
  --text-architects: 'Architects Daughter', cursive;
  --text-russo: 'Russo One', cursive;
  --text-abel: 'Abel', cursive;
  --text-pacifico: 'Pacifico', cursive;
  --text-bangers: 'Bangers', cursive;
}

.drink-card {
  align-items: center;
  background-color: var(--color-secund);
  border-radius: 20px;
  box-shadow: 0 0 20px rgb(200, 200, 200);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 50px auto 0;
  width: 70%;
}

.drink-card img {
  border-radius: 20px 20px 0 0;
  height: auto;
  width: 100%;
}

.drink-card p {
  font-family: var(--text-pacifico);
  font-size: 35px;
  margin-bottom: 30px;
  margin-top: 20px;
}

.buttons-filter-drinks button {
  background-color: var(--color-secund);
  border: none;
  color: var(--bg-color);
  font-family: var(--text-russo);
  margin-top: 15px;
  width: 80%;
}

.buttons-filter-drinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.meal-card {
  align-items: center;
  background-color: var(--color-secund);
  border-radius: 20px;
  box-shadow: 0 0 20px rgb(200, 200, 200);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 50px auto 0;
  width: 70%;
}

.meal-card img {
  border-radius: 20px 20px 0 0;
  height: auto;
  width: 100%;
}

.meal-card p {
  font-family: var(--text-pacifico);
  font-size: 35px;
  margin-bottom: 30px;
  margin-top: 20px;
}

.buttons-filter button {
  background-color: var(--color-secund);
  border: none;
  color: var(--bg-color);
  font-family: var(--text-russo);
  margin-top: 15px;
  width: 45%;
}

.buttons-filter {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

footer {
